.nav {
grid-area: n;
padding: 20px;
border-radius: 15px;
background-color: #212437;
box-shadow: 0 0 10px rgba(0, 0, 0, .5);
}

.item a {
text-decoration: none;
color: #fff;
transition: color .2s linear;
}
.item a:hover{
    color: #00d7b2;
}
  .item a.active {
      color: #00d7b2;
}